export default {
  // 解析URL参数， 传入search部分
  parseUrl(url) {
    var result = {};
    var query = url.replace('?', '');
    var queryArr = query.split('&');
    queryArr.forEach(function(item) {
      var value = item.split('=')[1];
      var key = item.split('=')[0];
      result[key] = value;
    });
    return result;
  },
};
