<template>
  <div class="auth-page">
    <span class="iconfont iconweibiaoti1" />
    <div>Loading...</div>
  </div>
</template>
<script>
import Config from "@/config";
import tools from "@/utils/tools";
export default {
  mounted() {
    const isAuthed = localStorage.getItem("isAuthed");
    const p = window.location.search;
    const s = tools.parseUrl(p);
    // 已经有了code，存全局
    if (s && s.code) {
      sessionStorage.setItem("code", s.code);
      sessionStorage.setItem("openid", s.openid);
      sessionStorage.setItem("isSessionAuthed", true);
      localStorage.setItem("isAuthed", true);
      top.location.href = `${window.location.origin}?#/home`;
      return;
    }
    let scope = "snsapi_userinfo";
    if (isAuthed) {
      scope = "snsapi_base";
    }
    const href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${
      Config.appId
    }&redirect_uri=${encodeURIComponent(
      window.location.origin
    )}&response_type=code&scope=${scope}&state=STATE#wechat_redirect`;
    top.location.href = href;
  }
};
</script>
<style lang="less">
.auth-page {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
  font-style: italic;
  color: #222;
  @keyframes loadingAnime {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(-5px);
    }
  }
  .iconfont {
    font-size: 32px;
    display: block;
    animation-iteration-count: infinite;
    animation-duration: 800ms;
    animation-name: loadingAnime;
    animation-direction: alternate;
  }
}
</style>