import axios from 'axios';
import Config from '../config';
import qs from 'qs';

const Module = {
  namespaced: true,
  state: {
    userInfo: {}, // 用户信息
    wxConfigData: {},
    wxToken: '',
    wxCode: '123',
    wxOpenId: '',
  },
  mutations: {
    saveCodeAndOpenId(state, payload) {
      state.wxCode = payload.code || '';
      state.wxOpenId = payload.openid || '';
    },
    saveWxConfigData(state, payload) {
      state.wxConfigData = payload.data;
    },
    saveWxToken(state, payload) {
      state.wxToken = payload.data;
    },
    saveUserInfo(state, payload) {
      console.log('再这里：', payload);
      state.userInfo = payload.data;
    },
  },
  actions: {
    /** 保存code和appid **/
    async saveCodeAndOpenId(context, payload) {
      context.commit({
        type: 'saveCodeAndOpenId',
        data: payload,
      });
    },
    /** 获取自定义的token **/
    async getToken({ state, commit }, payload) {
      try {
        const url = `/wxChat/getToken.action`;
        const headers = {
          code: sessionStorage.getItem('code') || '',
          openid: sessionStorage.getItem('openid') || '',
        };

        console.log('发起token请求：', headers, Config.baseUrl, url);

        const msg = await axios({
          url: `${Config.baseUrl}${url}`,
          method: 'GET',
          headers: {
            'Content-Type': 'application/json; charset=utf-8',
            ...headers,
          },
          withCredentials: false,
        });

        if (msg.status === 200 && msg.data && msg.data.success) {
          commit({
            type: 'saveWxToken',
            data: msg.data.token,
          });
        }
        console.log('获取token返回：', msg);
        return msg;
      } catch (e) {
        console.log('请求错误');
      }
    },
    /** 获取用户信息 **/
    async getUserInfo({ state, rootState, commit }, payload) {
      try {
        const url = `/user/findUserByToken.action`;
        const headers = {
          token: state.wxToken,
        };
        const msg = await axios({
          url: `${Config.baseUrl}${url}`,
          method: 'GET',
          headers: {
            'Content-Type': 'application/json; charset=utf-8',
            ...headers,
          },
          withCredentials: false,
        });

        if (msg.status === 200 || msg.status === 304) {
          console.log('你在TM逗我吗：', msg);
          commit({
            type: 'saveUserInfo',
            data: msg.data.data,
          });
        }
        return msg;
      } catch (e) {
        console.log('请求错误');
      }
    },
    /** 修改用户信息 **/
    async updateUserInfo({ state, rootState, commit }, payload) {
      const url = `/user/modifyUserByToken.action`;
      const headers = {
        token: state.wxToken,
      };
      const msg = await axios({
        url: `${Config.baseUrl}${url}`,
        method: 'POST',
        headers: {
          'Content-Type': 'x-www-form-urlencoded;charset=UTF-8',
          ...headers,
        },
        data: payload,
        withCredentials: false,
      });
      return msg;
    },

    /** 微信初始化所需信息 **/
    async getWechartConfig({ state, commit }, payload) {
      console.log('获取微信初始化所需信息:', payload);
      try {
        const url = `/wxChat/getSignature.action`;
        const headers = {
          url: payload,
          token: state.wxToken,
        };
        const msg = await axios({
          url: `${Config.baseUrl}${url}`,
          method: 'GET',
          headers: {
            'Content-Type': 'application/json; charset=utf-8',
            ...headers,
          },
          withCredentials: false,
        });
        console.log('获取微信初始化所需信息有返回？', msg);
        if (msg.status === 200 || msg.status === 304) {
          commit({
            type: 'saveWxConfigData',
            data: msg.data,
          });
        }
        return msg;
      } catch (e) {
        console.log('请求错误');
      }
    },
  },
};

export default Module;
